import { Component } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { ModalComponent } from '../modal/modal.component';
import { Router } from '@angular/router';
import { HttpService } from '../shared/http.service';
import Swal from 'sweetalert2';
import { ToastController } from '@ionic/angular';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page {
  constructor(public popoverController: PopoverController, private router: Router, private http: HttpService,
    private toastCtrl: ToastController, route: ActivatedRoute) {
    route.params.subscribe(val => {
      this.PopupModel = false;
      this.getCategoryList()
      //this.getSubcategoryList()
      this.getUnitList()
    });
  }

  ngOnInit() {

  }

  MediaFileSelected(event) {
    this.selectedFile = event.target.files[0] as File;

  }

  selectedFile: File = null;

  userdetails: any = JSON.parse(window.atob(localStorage.getItem("24hrs-user-data")));

  public date: string = new Date().toISOString();
  Category: any = '';
  subcategory: any = '';
  productname: any = '';
  description: any = '';
  cost: any = ''
  weight: any = '';
  categoryName: any = '';
  subcategoryName: any = '';
  categoryList: any = [];
  subcategoryList: any = [];
  unitList: any = []

  PopupModel: any = false;
  subcategoryPopupModel: any = false;

  async upload(ev: any) {
    const popover = await this.popoverController.create({
      component: ModalComponent,
      cssClass: 'my-custom-class',
      event: ev,
      translucent: true
    });
    await popover.present();

    const { role } = await popover.onDidDismiss();
    console.log('onDidDismiss resolved with role', role);
  }


  backToprivious() {
    this.PopupModel = false;
    this.Category = ''

  }

  ScBackToprivious() {
    this.subcategoryPopupModel = false;
    this.subcategory = ''

  }
  subCategoryNotfound = false;
  addproduct() {

    this.router.navigate(['/myproducts'])
  }
  createCategory() {
    this.PopupModel = false;
    const catData = {
      category_name: this.categoryName,
      token: this.userdetails.id,
      created_at: this.date
    }

    this.http.post('/create_category', catData).subscribe((response: any) => {
      console.log(response);

      if (response.success == "true") {
        this.Category = ''
        this.categoryName = ''
        this.getCategoryList()


      }
    }, (error: any) => {
      console.log(error);

    }
    );

  }


  category_tbid: any = " ";
  popupModel(data) {

    if ( data.category == "categoryPopup") {
      this.PopupModel = true;

    }
    if ( data.subcategory == "subcategoryPopup") {
      this.subcategoryPopupModel = true;

    }
    
    const formdata = new FormData();
    formdata.append("category", data.category);
    console.log(data.category);
    this.category_tbid = data.category
    const obj = {
      category: data.category
    }
    this.http.post('/read_subcategory', obj).subscribe((response: any) => {
      console.log(response.records);
      this.subcategoryList = response.records
      console.log(this.subcategoryList);
      this.subCategoryNotfound = false;

    }, (error: any) => {
      console.log(error);
      this.subCategoryNotfound = true;
      this.subcategoryList = [];
    }
    );



  }


  createSubcategory() {
    this.subcategoryPopupModel = false;
    const subcatData = {
      category: this.Category,
      subcategory_name: this.subcategoryName

    }

    this.http.post('/create_subcategory', subcatData).subscribe((response: any) => {
      console.log(response);

      if (response.success == "true") {
        console.log("test");
        this.Category = ''
        this.subcategory = ''
        this.subcategoryName = ''



      }

    }, (error: any) => {
      console.log(error);
    }
    );

  }


  getCategoryList() {

    this.http.get('/read_all_category',).subscribe((response: any) => {
      console.log(response.records);

      this.categoryList = response.records
      console.log(response.records);
      console.log(this.categoryList);


    }, (error: any) => {
      console.log(error);
    }
    );
  }








  addToProduct() {
    const productData = {
      category: this.Category,
      subcategory: this.subcategory,
      product_name: this.productname,
      description: this.description,
      cost: this.cost
    }

    this.http.post('/update_product', productData).subscribe((response: any) => {
      console.log(response);
      if (response.success == "true") {
        // const Toast = Swal.mixin({
        //   toast: true,
        //   position: 'top-end',
        //   showConfirmButton: false,
        //   timer: 1000,
        //   timerProgressBar: true,
        //   didOpen: (toast) => {
        //     toast.addEventListener('mouseenter', Swal.stopTimer)
        //     toast.addEventListener('mouseleave', Swal.resumeTimer)
        //   }
        // })

        // Toast.fire({
        //   icon: 'success',
        //   title: 'Signed in successfully'
        // })

        this.addproduct()
      }
    }, (error: any) => {
      console.log(error);
    }
    );

  }

  getUnitList() {
    this.http.post('/read_unit', '').subscribe((response: any) => {
      console.log(response);
      this.unitList = response.records;
      console.log(this.unitList);


    }, (error: any) => {
      console.log(error);
    }
    );
  }


  image: any;



  onClickSubmit(data) {
    this.image = this.selectedFile;
    console.log(this.image)
    const formdata = new FormData();
    formdata.append("category", this.category_tbid);
    formdata.append("subcategory", data.subcategory);
    formdata.append("product_name", data.product_name);
    formdata.append("cost", data.cost);
    formdata.append("unit", data.unit);
    formdata.append("description", data.description);
    formdata.append("product_image", this.image);
    formdata.append("weight", data.weight)

    console.log('formData: ', formdata.getAll('category'), formdata.getAll('product_image'));

    // var body = "category = " + data.category + "subcategory = " + data.subcategory + "product_name = " + data.product_name + "cost = " + data.cost + "unit = " + data.unit + "product_image = " + this.image + "description = " + data.description;
    // console.log(body);

    this.http.postFormData("/create_product", formdata).subscribe((response: any) => {
      console.log(response);

      if (response.success == "true") {
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 1000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
          }
        })

        Toast.fire({
          icon: 'success',
          title: 'Product Added Successfully '
        })
        this.router.navigate(['/myproducts']);
      }
      if (response.success == "false" || response.message == "Product Already Exist." ) {
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 1000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
          }
        })
      

        Toast.fire({
          icon: 'success',
          title: 'Product Already Exist'
        })
        this.router.navigate(['/myproducts']);
      }  

    }, (error: any) => {
      console.log(error);
    }
    );
  }

}
